body{
    background-color: $light-coffee;
    font-family: Arial, Helvetica, sans-serif;
}

//reset bootstrap


.btn-custom {
    color: white;
    font-weight: 500;
    background-color: $dark-coffee;
    border-color: $dark-coffee;
    margin: $l-size 0;
    margin-right: $m-size;
  }
  .btn-custom:hover,
  .btn-custom:focus,
  .btn-custom:active,
  .btn-custom.active {
    background-color: $dark-coffee;
    border-color: $dark-coffee;
    opacity: 0.75;
    box-shadow: none;
  }


.list-group-item:last-child{
    margin-bottom: $xs-size;
}

